import React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"

const Tags = () => {
  const categoryData = useStaticQuery(graphql`
    {
      allDirectory(filter: { relativeDirectory: { regex: "/posts$/" } }, sort: { order: DESC, fields: name }) {
        nodes {
          name
          id
        }
      }
    }
  `)

  const Tag = categoryData.allDirectory.nodes.map(category => {
    return (
      <Link className="post-tag" to={`#${category.name}`} key={category.id}>
        {category.name}
      </Link>
    )
  })

  return (
    <div>
      <div className="tag_wrap">{Tag}</div>
    </div>
  )
}

export default Tags
